
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 50%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
}

.head {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  position: relative;
}

.closeButton {
  position: absolute;
  right: 0;
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  padding: 0;
}

.title {
  margin: 0;
}

.filtersContainer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
  background-color: #f4f5f4;
}

.filterButton {
  padding: 10px 15px;
  border: 1px solid #ccc;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  background-color: #f4f5f4;
  color: #000;
}

.filterButton.selected {
  background-color: #99cc33;
  color: #fff;
}

.applyButton {
  padding: 10px 20px;
  background-color: #003333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: fit-content;
}

.buttons{
  display: flex;
  gap: 13px;
  justify-content: end;
}

/* IngredientsTable.module.css */

/* Стили для таблицы */
.table {
  border-collapse: collapse;
  font-family: Arial, sans-serif;
  width: 554px;
}

.table th,
.table td {
  width: fit-content;
  text-align: left;
  border: 1px solid #ddd;
}

.table th {
  background-color: #f4f4f4;
  font-weight: bold;
  color: #333;
}

.table tr:hover {
  background-color: #f9f9f9;
}

/* Стили для кнопки */
.button {
  background-color: #f4f4f4;
  border: 1px solid #000;
  padding: 8px 16px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100% ;
  margin: 0;
}

.button:hover {
  background-color: #ddd;
}
.lala{
  width: fit-content;
